@use 'variables' as *;
@use 'mixin' as *;

@media (min-width: $mobile) {
  
  .my-study {
    display: flex;
    flex-direction: column;
    font-family: Pretendard, -apple-system, Roboto, Helvetica, sans-serif;
    gap: 12px;
    padding: 0 $mobilePadSide;

    &__header {
      display: flex;
      width: 100%;
      align-items: center;
      gap: 4px;
      font-size: 20px;
      color: var(--text-primary, #F0F0F0);
      font-weight: 700;
      line-height: 28px;
      justify-content: space-between;
      .btn-refresh{
        display: inline-flex;
        background: transparent;
        align-items: center;
        border: 0;
        color: var(--text-secondary, #626262);
        /* Body/SS */
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 150%; /* 21px */
      }
    }

    &__title {
      color: var(--text-primary, #F0F0F0);
      i{
        vertical-align: middle;
      }
    }

    &__icon {
      width: 28px;
      height: 28px;
      flex-shrink: 0;
      object-fit: contain;
      object-position: center;
    }

    &__description {
      color: var(--text-primary, #F0F0F0);
      font-size: 15px;
      font-weight: 400;
      line-height: 23px;
    }

    &__topper{
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: 182px;
      gap: 24px;
      height: 190px;

      @media screen and (max-width: $pc) {
        grid-template-columns: 1fr;
        grid-template-rows: auto;
        height: auto;

      }
    }

    &__stats-grid {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 12px;
      min-width: 460px;
    }

    &__divider{
      width: auto;
      height: 1px;
      background: var(--border-default, #E0E0E0);
      margin: 24px $mobilePadSide 20px;
    }

    &__list {
      display: flex;
      flex-direction: column;
    }
  }

  .stats-card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px 24px;
    border-radius: 8px;
    border: 1px solid var(--border-default, #464646);
    background: var(--bg-subtle, #2F2F2F);
    overflow: hidden;
    position: relative;

    &__content {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 12px;
      align-self: stretch;
      position: relative;
      z-index: 1;
    }

    &__header {
      display: flex;
      align-items: center;
      gap: 8px;
    }

    &__icon {
      display: flex;
      width: 28px;
      height: 28px;
      justify-content: center;
      align-items: center;
      border-radius: 9999px;
      flex-shrink: 0;

      &--orange {
        background: #FFEDD5;
      }

      &--yellow {
        background: #FEF9C3;
      }

      &--red {
        background: #FFEDD5;
      }

      &--orange-alt {
        background: #FFEDD5;
      }

      svg {
        width: 16px;
        height: 16px;
      }
    }

    &__label {
      flex: 1;
      color: var(--text-secondary, #7B7B7B);
      font-size: 15px;
      font-weight: 400;
      line-height: 150%;
      white-space: nowrap;
    }

    &__value-wrapper {
      display: flex;
      align-items: center;
      gap: 4px;
      align-self: stretch;
    }

    &__value {
      display: inline-flex;
      align-items: center;
      color: var(--text-primary, #F0F0F0);
      text-align: center;
      font-size: 22px;
      font-weight: 700;
      line-height: 140%;
    }

    &__unit {
      color: var(--text-secondary, #7B7B7B);
      font-size: 12px;
      font-weight: 500;
      line-height: 150%;
      margin-left: 4px;
    }

    &__progress {
      height: 8px;
      width: 100px;
      align-self: center;
      border-radius: 9999px;
      background: #F0F0F0;
      position: relative;
      overflow: hidden;
    }

    &__progress-bar {
      height: 8px;
      border-radius: 9999px;
      background: var(--orange-100, #FF7A00);
      position: absolute;
      left: 0;
      top: 0;
    }

    &__decoration {
      width: 121px;
      height: 121px;
      border-radius: 50%;
      position: absolute;
      right: -28px;
      top: -60px;
      pointer-events: none;

      &--orange {
        background: rgba(255, 122, 0, 0.1);
      }

      &--yellow {
        background: rgba(255, 234, 0, 0.1);
      }

      &--red {
        background: rgba(255, 0, 0, 0.1);
      }

      &--orange-alt {
        background: rgba(255, 166, 0, 0.1);
      }
    }
  }

  .my-study {
    .stats-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 8px 20px;
      gap: 4px;


      &__label {
        font-size:  12px;
        font-weight: 500;
        line-height: 150%;
        color: var(--text-secondary, #626262);
        text-align: center;
      }

      &__value {
        font-size: 18px;
        font-weight: 700;
        line-height: 140%;
        color: var(--text-primary, #212121);
        text-align: center;

        &--level {
          border-radius: 100px;
          border: 1px solid var(--border-default, #E0E0E0);
          background: var(--bg-subtle_02, #F9FAFB);

          border-radius: 999px;
          padding: 4px 8px;

          color: var(--orange-100, #FF7A00);

          /* Caption/SM */
          font-family: Pretendard;
          font-size: 12px;
          font-style: normal;
          font-weight: 500;
          line-height: 150%; /* 18px */
        }
      }
      &:nth-child(1){
        border-right: 1px solid var(--border-default, #464646);;
        border-bottom: 1px solid var(--border-default, #464646);;
      }
      &:nth-child(2){
        border-bottom: 1px solid var(--border-default, #464646);;
      }
      &:nth-child(3){
        border-right: 1px solid var(--border-default, #464646);;
      }
    }

    &__divider{
      width: auto;
      height: 1px;
      background: var(--border-default, #E0E0E0);
      margin: 24px $mobilePadSide 20px;
    }

    &__list {
      display: grid;
      gap: 20px;
      @media screen and (min-width: $pc) {
        grid-template-columns: repeat(3, 1fr);
      }
      @media screen and (max-width: $pc) {
        grid-template-columns: repeat(2, 1fr);
        gap: 12px;
      }
    }
  }

  .ai-agent-info {
    position: relative;
    display: flex;
    align-items: center;
    gap: 20px;
    padding: 24px;
    height: 100%;

    border-radius: var(--radius-md, 8px);
    border: 1px solid var(--border-default, #464646);
    background: var(--bg-default, #1D1D1D);

    /* inner shadow */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.20) inset;

    overflow: hidden;
    &__decoration{
      width: 121px;
      height: 121px;
      border-radius: 50%;
      position: absolute;
      right: -28px;
      bottom: -57px;
      pointer-events: none;
      background: rgba(255, 122, 0, 0.1);
    }



    &__icon {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      flex: 0 0 80px;
      width: 80px;
      height: 80px;

      border-radius: 16px;
      border: 1px solid var(--orange-100, #FF7A00);
      background: var(--white, #FFF);
      box-shadow: 0 2px 4px 1px rgba(0, 0, 0, 0.05) inset;
    }

    &__content {
      flex: 1;
      display: flex;
      flex-direction: column;
      gap: 8px;
    }

    &__header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 16px;
    }

    &__title {
      margin: 0;
      color: var(--text-primary, #F0F0F0);
      /* Heading/H4 */
      font-size: 18px;
      font-weight: 700;
      line-height: 140%;
      white-space: nowrap;
    }

    &__badge {
      display: inline-flex;
      color: var(--white, #FFF);

      /* Caption/SM */
      font-size: 12px;
      font-weight: 500;
      line-height: 150%;

      display: inline-flex;
      padding: 2px 8px;
      flex-direction: column;
      align-items: flex-end;
      border-radius: 4px;
      background: var(--orange-100, #FF7A00);
      white-space: nowrap;

    }

    &__description {
      margin: 0;
      color: var(--text-primary, #F0F0F0);
      /* Body/SS */
      font-size: 14px;
      font-weight: 400;
      line-height: 150%; /* 21px */

      // overflow: hidden;
      // text-overflow: ellipsis;
      // display: -webkit-box;
      // -webkit-line-clamp: 3;
      // -webkit-box-orient: vertical;
    }
  }

  .learning-card {
    display: flex;
    flex-direction: column;
    gap: 16px;
    padding: 20px 16px;
    background: var(--bg-subtle, #F9FAFB);
    border: 1px solid var(--border-default, #E0E0E0);
    border-radius: var(--radius-lg, 12px);

    &__topper{
      display: grid;
      grid-template-columns: 100px 1fr;
      grid-template-rows: 70px;
      gap: 12px;
      .content{
        display: flex;
        flex-direction: column;
        gap: var(--spacing-xs, 4px);
      }
    }
    &__thumbnail{
      min-height: 68px;
      height: 100%;
      border-radius: var(--radius-sm, 4px);
      overflow: hidden;
      background: var(--bg-subtle_02);
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }


    &__category-badge {
      display: inline-flex;
      align-self: flex-start;
      padding: 0 12px;
      height: 20px;
      background: var(--green, #37CE00);
      border-radius: 100px;
      color: var(--white, #FFF);
      font-size: 13px;
      font-weight: 600;
      line-height: 150%;
    }

    &__title {
      overflow: hidden;
      color: var(--text-primary, #212121);
      text-overflow: ellipsis;
      font-size: 15px;
      font-style: normal;
      font-weight: 400;
      line-height: 150%;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }

    &__content {
      display: flex;
      gap: 12px;
      padding: 16px;
      background: var(--bg-default, #1D1D1D);
      border: 1px solid var(--border-default, #E0E0E0);
      border-radius: var(--radius-md, 8px);
      height: 76px;
    }

    &__ai-badge {
      display: inline-flex;
      align-self: flex-start;
      padding: 0 var(--gap-normal, 8px);
      height: 20px;
      white-space: nowrap;
      border-radius: 100px;
      color: var(--white, #FFF);
      font-size: 13px;
      font-weight: 600;
      line-height: 150%;

      @include GradientBorder;
    }

    &__description {
      margin: 0;
      color: var(--text-secondary, #626262);
      font-size: 14px;
      font-weight: 400;
      line-height: 150%;
      text-overflow: ellipsis;
      
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }

    &__tags {
      display: flex;
      gap: 4px;
      flex-wrap: wrap;
    }

    &__tag {
      display: inline-flex;
      padding: 4px 8px;
      background: var(--orange-100, #FF7A00);
      border-radius: 100px;
      color: var(--white, #FFF);
      font-size: 11px;
      font-weight: 500;
      line-height: 150%;
    }
  }

  .term-item {
    display: flex;
    flex-direction: column;
    padding: 20px;
    border-radius: var(--radius-lg, 12px);
    background: var(--bg-subtle, #F9FAFB);
    transition: border-color 0.2s ease;
    border: 1px solid var(--border-default, #464646);
    gap: 12px;
    // &--active {
    //   border-color: var(--orange-100, #FF7A00);

    //   .term-item__content {
    //     display: block;
    //   }
    //   .term-item__toggle{
    //     svg{
    //       transform: rotate(180deg);
    //     }
    //   }
    // }
    &__header {
      display: flex;
      align-items: center;
      gap: 8px;
      cursor: default;
    }

    &__badge {
      display: flex;
      height: 24px;
      padding: 2px 6px;
      align-items: center;
      justify-content: center;
      border-radius: 6px 2px;
      background: var(--orange-200, #E55204);
      color: var(--white, #FFF);
      font-size: 13px;
      font-weight: 600;
      line-height: 150%;
      flex-shrink: 0;
    }

    &__title {
      flex: 1;
      color: var(--text-primary, #212121);
      font-size: 18px;
      font-weight: 700;
      line-height: 140%;
      margin: 0;
    }

    &__toggle {
      display: none;
      // display: flex;
      // align-items: center;
      // justify-content: center;
      // width: 24px;
      // height: 24px;
      // background: transparent;
      // border: none;
      // cursor: pointer;
      // padding: 0;
      // flex-shrink: 0;
      // color: var(--text-primary, #212121);

      // svg {
      //   width: 24px;
      //   height: 24px;
      // }
    }

    &__content {
      // display: none;
      margin-top: 12px;
    }


    &__description {
      margin: 0;
      color: var(--text-primary, #212121);
      font-size: 14px;
      font-weight: 400;
      line-height: 150%;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp: 6;
      -webkit-box-orient: vertical;
    }
  }

  .my-study{
    &__words{
      display: grid;
      gap: 20px;

      &.today-study{
        @media screen and (min-width: $pc) {
          grid-template-columns: repeat(3, 1fr);
        }
        @media screen and (max-width: $pc) {
          grid-template-columns: repeat(2, 1fr);
          gap: 12px;
        }
      }

    }
  }

  .key-eco-word{
    .my-study__words{
      gap: 12px;
      .term-item{
        &--active {
          border-color: var(--orange-100, #FF7A00);
    
          .term-item__content {
            display: block;
          }
          .term-item__toggle{
            svg{
              transform: rotate(180deg);
            }
          }
        }
        &__toggle {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 24px;
          height: 24px;
          background: transparent;
          border: none;
          cursor: pointer;
          padding: 0;
          flex-shrink: 0;
          color: var(--text-primary, #212121);
    
          svg {
            width: 24px;
            height: 24px;
          }
        }
        &__content {
          display: none;
          margin-top: 12px;
        }
      }

    }
  }
}